export default {
	data() {
		return {
			selectEd: selectEd, // 勾选圆框 黑色
			select: select, // 未勾选圆框 白色
			noTipsImg: select,
			skipMaskBg: skipMaskBg,
			dateLists: this.dateListM
		};
	},
	props: [
		'dateListM'
	],
	// watch: {
